.main-nav {
  @media (max-width: $mobile-width-only) {
    display: flex;
    justify-content: center;
    width: 100%;
    position: absolute;
    top: 40px;
    z-index: 1;
  }
}

.main-nav-list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;

  display: flex;
  flex-direction: column;
  text-align: center;

  @media (min-width: $tablet-width) {
    width: 440px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
}

.main-nav-list__link {
  width: 100%;
  padding: 22px 0 24px 0;
  color: $black;
  background-color: $white;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px; /* 100% */
  text-transform: uppercase;
  display: block;
  text-decoration: none;
}
